import {commumal} from '../public'
var auditing={
    passValue:"",
    methods:{
        init(){
            $("#switchCP").on("change",commumal.isThrough);
            $(".btnBack").on("click",commumal.back);
            $(".submit").on("click",auditing.events.submit)
        }
    },
    events:{
        filldata(param){
             $.defs.loadingshow();
             $.defs.myPost({
                m:"multi_result",
                SP_NAME:"SP_ATDC_PRE_LOAD",
                ATDC_KY:param.content.atcdKy
             }).then((data)=>{
                 $.defs.loadinghide();
                 let back=data.result || data;
                 if(back.RETURN_CODE==0){
                     let {bill_id,atdc_info,atdc_auditing}=data;
                    $("span.billId").text(bill_id.ATDC_ID);
                    $("span.through").text(atdc_info.ATDC_STTS_KY_DESC);
                    $.defs.fillData({
                        container:".examine",
                        data:atdc_info
                    })
                    if(atdc_info.BSTR_DESC){
                        $(".areaRange").html(atdc_info.BSTR_NAME+"("+atdc_info.BSTR_DESC+")");
                    }
                    if(atdc_info.ATDC_STS>=141&&atdc_info.ATDC_STS<199){
                        $(".resultTime").text(atdc_info.FEEDBACK_DT)
                        $(".resultCommentp").text(atdc_info.FEEDBACK_DESC)
                    }else{
                        $(".resultComment").hide()
                    }
                    if(atdc_info.SYSV_ATDC_TYPE=="BSTP"){
                        $(".guest").hide()
                        if(atdc_info.ATDC_STS>=141&&atdc_info.ATDC_STS<199){
                            $(".trip span").html(atdc_info.ATDC_DAY+"/"+atdc_info.LAST_ATDC_DAY)
                            $(".travel span").html(atdc_info.ATDC_HUMAN_CNT+"/"+atdc_info.LAST_ATDC_HUMAN_CNT)
                        }else{
                            $(".trip span").html(atdc_info.ATDC_DAY+"/-")
                            $(".travel span").html(atdc_info.ATDC_HUMAN_CNT+"/-")
                        }

                    }else{
                        $(".trip").hide()
                        $(".travel").hide()
                        $(".guest span").html(atdc_info.ATDC_HUMAN_CNT+"/"+atdc_info.LAST_ATDC_HUMAN_CNT)

                    }
                    let html=auditing.events.audHtml(atdc_auditing);
                    $(".addComment").append(html)
                    if(atdc_info.AUDIT_BUTTON_FEATURE=="AGREE"){
                        $(".weui-cell_switch").addClass("dn");
                        $(".footerBtn .submit").text("已知晓");
                        $(".shenhekong .weui-label").html("备注")
                    }else if(atdc_info.AUDIT_BUTTON_FEATURE==""){
                        history.go(-1)
                    }
                 }else{
                    $.defs.msg(back.RETURN_MESSAGE)
                 }

             })
        },
        audHtml(data){
            let html='';
            for(let i=0;i<data.length;i++){
                html+=`
                 <div class="clearfix borderDesc  f12">
                  <p>
                    <span>${data[i].MEME_NAME}-${data[i].STLG_DTM}</span>
                    <span class="fr">${data[i].STLG_STS_DESC}</span>
                  </p>
                   <p class="db">备注 :${data[i].STLG_COMMENT}</p>
                 </div>
                `
            }
            return html;
        },
        submit(){
            commumal.confirm({
                 click:()=>{
                     auditing.events.submitdata()
                 }
            })
        },
        submitdata(){
            $.defs.loadingshow();
            let param=auditing.passValue;
            $.defs.myPost({
                m:"UPDATE",
                SP_NAME:"SP_ATDC_ATTENDANCE_INFO_STATUS_UPDATE",
                ATDC_COMMENT:$(".lookThrough").val(),
                REVIEW_IND:$("#switchCP").prop("checked")?"Y":"N",
                ATDC_KY:param.content.atcdKy
            }).then((data)=>{
                $.defs.loadinghide();
                if(data.RETURN_CODE==0){
                    weui.toast('操作成功', 2500);
                    setTimeout(()=> {
                         history.go(-1)
                    },2600);
                }
            })
        }
    },
    config:{
        init(){
            $(".top .huan").text("待审核>外勤管理");
            let param=spspa.getMessage("auditing");
             auditing.passValue=param;
              spspa.clearMessage();
              if(auditing.passValue){
                  auditing.events.filldata(param)
              }else{
                  history.go(-1)
              }
        }
    }
}
$(function(){
    auditing.config.init();
    auditing.methods.init();
})


